import Vue from 'vue'
import Router from 'vue-router'
import GenerateBalloon from '@/components/GenerateBalloon'

Vue.use(Router)

import jQuery from 'jquery'
global.jquery = jQuery
global.$ = jQuery
window.$ = window.jQuery = require('jquery')

export default new Router({
  routes: [
    {
      path: '/',
      name: 'GenerateBalloon',
      component: GenerateBalloon
    }
  ]
})


