<template>
  <div>
    <h1>Balloon generator</h1>
    <div class="box">
      <div class="unit canvas" :class="{ bgdark: isBgdark, bglight: isBglight }">
        <div class="wrap">
          <div class="balloon" :style="style">
            <p v-html="sampleText.replace(/\n/g,'<br/>')"></p>
            <div class="tail" :style="subStyle"></div>
          </div>
        </div>
        <div class="bottons">
          <button @click="toggleClass">背景色:暗め透過</button>
          <button @click="toggleClass">背景色:明るめ透過</button>
        </div>
      </div>
      <div class="unit controller">
        <h2>Text</h2>
        <p><textarea v-model="sampleText"></textarea></p>

        <h2>background</h2>
        <p><input type="text" maxlength="7" minlength="4" v-model="input.bgcolor"> <span class="samplebox" :style="{ background: input.bgcolor}">{{ input.bgcolor }}</span></p>

        <h2>color</h2>
        <p><input type="text" maxlength="7" minlength="4" v-model="input.color"> <span class="samplebox" :style="{ background: input.color}">{{ input.color }}</span></p>

        <h2>font size (px)</h2>
        <p><input type="range" min="8" max="40" v-model="input.fs"><output>{{ input.fs }}</output></p>

        <h2>line height</h2>
        <p><input type="range" min="0" step="0.1" max="4" v-model="input.lh"><output>{{ input.lh }}</output></p>

        <h2>letter spacing (em)</h2>
        <p><input type="range" min="-1" step="0.01" max="2" v-model="input.ls"><output>{{ input.ls }}</output></p>

        <h2>text align</h2>
        <div class="arrow">
          <select v-model="style.textAlign">
            <option>left</option>
            <option>center</option>
            <option>right</option>
          </select>
        </div>

        <h2>margin</h2>
        <p><span>top:   </span><input type="range" min="0" max="200" v-model="input.mt"><output>{{ input.mt }}</output></p>
        <p><span>right: </span><input type="range" min="0" max="200" v-model="input.mr"><output>{{ input.mr }}</output></p>
        <p><span>bottom:</span><input type="range" min="0" max="200" v-model="input.mb"><output>{{ input.mb }}</output></p>
        <p><span>left:  </span><input type="range" min="0" max="200" v-model="input.ml"><output>{{ input.ml }}</output></p>

        <h2>padding</h2>
        <p><span>top:   </span><input type="range" min="0" max="200" v-model="input.pt"><output>{{ input.pt }}</output></p>
        <p><span>right: </span><input type="range" min="0" max="200" v-model="input.pr"><output>{{ input.pr }}</output></p>
        <p><span>bottom:</span><input type="range" min="0" max="200" v-model="input.pb"><output>{{ input.pb }}</output></p>
        <p><span>left:  </span><input type="range" min="0" max="200" v-model="input.pl"><output>{{ input.pl }}</output></p>

        <h2>width</h2>
        <p><span><button @click="auto('w')">auto</button></span><input type="range" min="0" max="200" :input="set('w')" v-model="input.w"><output>{{ input.w }}</output></p>

        <h2>border radius (0 ~ 100px)</h2>
        <p><span>top:   </span><input type="range" min="0" max="100" v-model="input.bt"><output>{{ input.bt }}</output></p>
        <p><span>right: </span><input type="range" min="0" max="100" v-model="input.br"><output>{{ input.br }}</output></p>
        <p><span>bottom:</span><input type="range" min="0" max="100" v-model="input.bb"><output>{{ input.bb }}</output></p>
        <p><span>left:  </span><input type="range" min="0" max="100" v-model="input.bl"><output>{{ input.bl }}</output></p>

        <h2>balloon tail position</h2>
        <div class="arrow">
          <select v-model="baloonPositon" @change="pos()">
            <option>top</option>
            <option>right</option>
            <option>bottom</option>
            <option>left</option>
          </select>
        </div>
        <div class="sub" v-if="baloonPositon === 'top'">
          <h3>position</h3>
          <div class="arrow">
            <select v-model="baloonPositonSub" @change="ppos()">
              <option>left</option>
              <option>center</option>
              <option>right</option>
            </select>
          </div>
          <div class="inbox" v-if="baloonPositonSub === 'left'">
            <p><span>left:  </span><input type="range" min="0" max="200" v-model="input.sub.ppl"><output>{{ input.sub.ppl }}</output></p>
          </div>
          <div class="inbox" v-if="baloonPositonSub === 'right'">
            <p><span>right: </span><input type="range" min="0" max="200" v-model="input.sub.ppr"><output>{{ input.sub.ppr }}</output></p>
          </div>

          <h3>size</h3>
          <p><span>bottom:</span><input type="range" min="0" max="100" v-model="input.sub.bb"><output>{{ input.sub.bb }}</output></p>
          <p><span>right: </span><input type="range" min="0" max="100" v-model="input.sub.br"><output>{{ input.sub.br }}</output></p>
          <p><span>left:  </span><input type="range" min="0" max="100" v-model="input.sub.bl"><output>{{ input.sub.bl }}</output></p>
        </div>
        <div class="sub" v-if="baloonPositon === 'right'">
          <h3>position</h3>
          <div class="arrow">
            <select v-model="baloonPositonSub" @change="ppos()">
              <option>top</option>
              <option>center</option>
              <option>bottom</option>
            </select>
          </div>
          <div class="inbox" v-if="baloonPositonSub === 'top'">
            <p><span>top:  </span><input type="range" min="0" max="200" v-model="input.sub.ppt"><output>{{ input.sub.ppt }}</output></p>
          </div>
          <div class="inbox" v-if="baloonPositonSub === 'bottom'">
            <p><span>bottom: </span><input type="range" min="0" max="200" v-model="input.sub.ppb"><output>{{ input.sub.ppb }}</output></p>
          </div>

          <h3>size</h3>
          <p><span>top:   </span><input type="range" min="0" max="100" v-model="input.sub.bt"><output>{{ input.sub.bt }}</output></p>
          <p><span>left:  </span><input type="range" min="0" max="100" v-model="input.sub.bl"><output>{{ input.sub.bl }}</output></p>
          <p><span>bottom:</span><input type="range" min="0" max="100" v-model="input.sub.bb"><output>{{ input.sub.bb }}</output></p>
        </div>
        <div class="sub" v-if="baloonPositon === 'bottom'">
          <h3>position</h3>
          <div class="arrow">
            <select v-model="baloonPositonSub" @change="ppos()">
              <option>left</option>
              <option>center</option>
              <option>right</option>
            </select>
          </div>
          <div class="inbox" v-if="baloonPositonSub === 'left'">
            <p><span>left:  </span><input type="range" min="0" max="200" v-model="input.sub.ppl"><output>{{ input.sub.ppl }}</output></p>
          </div>
          <div class="inbox" v-if="baloonPositonSub === 'right'">
            <p><span>right: </span><input type="range" min="0" max="200" v-model="input.sub.ppr"><output>{{ input.sub.ppr }}</output></p>
          </div>

          <h3>size</h3>
          <p><span>top:   </span><input type="range" min="0" max="100" v-model="input.sub.bt"><output>{{ input.sub.bt }}</output></p>
          <p><span>right: </span><input type="range" min="0" max="100" v-model="input.sub.br"><output>{{ input.sub.br }}</output></p>
          <p><span>left:  </span><input type="range" min="0" max="100" v-model="input.sub.bl"><output>{{ input.sub.bl }}</output></p>
        </div>
        <div class="sub" v-if="baloonPositon === 'left'">
          <h3>position</h3>
          <div class="arrow">
            <select v-model="baloonPositonSub" @change="ppos()">
              <option>top</option>
              <option>center</option>
              <option>bottom</option>
            </select>
          </div>
          <div class="inbox" v-if="baloonPositonSub === 'top'">
            <p><span>top:  </span><input type="range" min="0" max="200" v-model="input.sub.ppt"><output>{{ input.sub.ppt }}</output></p>
          </div>
          <div class="inbox" v-if="baloonPositonSub === 'bottom'">
            <p><span>bottom: </span><input type="range" min="0" max="200" v-model="input.sub.ppb"><output>{{ input.sub.ppb }}</output></p>
          </div>

          <h3>size</h3>
          <p><span>top:   </span><input type="range" min="0" max="100" v-model="input.sub.bt"><output>{{ input.sub.bt }}</output></p>
          <p><span>right: </span><input type="range" min="0" max="100" v-model="input.sub.br"><output>{{ input.sub.br }}</output></p>
          <p><span>bottom:</span><input type="range" min="0" max="100" v-model="input.sub.bb"><output>{{ input.sub.bb }}</output></p>
        </div>

        <!-- <h2>shadow</h2>
        <p><span>right:  </span><input type="range" min="0" max="100" v-model="input.bsr"><output>{{ input.bsr }}</output></p>
        <p><span>bottom: </span><input type="range" min="0" max="100" v-model="input.bsb"><output>{{ input.bsb }}</output></p>
        <p><span>blur:   </span><input type="range" min="0" max="100" v-model="input.bsbl"><output>{{ input.bsbl }}</output></p>
        <p><span>opacity:</span><input type="range" min="0" max="1" step="0.1" v-model="input.bso"><output>{{ input.bso }}</output></p>
        <p><span>color:  </span><input type="text" maxlength="7" minlength="4" v-model="input.bsc"> <span class="samplebox" :style="{ background: input.bsc}">{{ input.bsc }}</span></p> -->

      </div>
    </div>
    <div class="box2">
      <h2>Complete setting?</h2>
      <div class="unit">
        <dl>
          <dt>wrap element</dt>
          <dd>
            <label><input type="radio" value="div" v-model="cs.wrapElement"> div</label>
            <label><input type="radio" value="p" v-model="cs.wrapElement"> p</label>
          </dd>
        </dl>
        <dl>
          <dt>balloon tail element</dt>
          <dd>
            <label v-if="cs.wrapElement !== 'p'"><input type="radio" value="div" v-model="cs.btElement"> div</label>
            <label><input type="radio" value="span" v-model="cs.btElement"> span</label>
            <label><input type="radio" value="before" v-model="cs.btElement"> ::before</label>
            <label><input type="radio" value="after" v-model="cs.btElement"> ::after</label>
          </dd>
        </dl>
        <dl>
          <dt>How to export</dt>
          <dd>
            <label><input type="radio" value="scss" v-model="cs.exportType"> scss</label>
            <label><input type="radio" value="css" v-model="cs.exportType"> css</label>
          </dd>
        </dl>
        <button @click="generate">OK</button>
      </div>
    </div>
    <div id="complete" class="box2" :class="{ block: isComplete }">
      <h2>Complete -> export {{ cs.exportType }} !! ↓↓</h2>
      <h3>html</h3>
      <textarea id="html" class="mini"></textarea>
      <h3>{{ cs.exportType }}</h3>
      <textarea id="source"></textarea>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GenerateBalloon',
  data () {
    return {
      isBgdark: true,
      isBglight: false,
      isComplete: false,
      sampleText: 'サンプルテキスト',
      input: {
        fs: 18,
        lh: 1.6,
        ls: 0.02,
        color: '#ffffff',
        bgcolor: '#42b983',
        mt: 10,
        mr: 10,
        mb: 10,
        ml: 10,
        pt: 10,
        pr: 10,
        pb: 10,
        pl: 10,
        w: 300,
        bt: 10,
        br: 10,
        bb: 10,
        bl: 10,
        // bsr: 0,
        // bsb: 0,
        // bsbl: 0,
        // bso: 0,
        // bsc: '#000000',
        sub: {
          shape: 'type1',
          bt: 10,
          br: 10,
          bb: 10,
          bl: 10,
          ppt: 'inherit',
          ppl: '50%',
          ppb: '-20px',
          ppr: 'inherit',
        }
      },
      style: {
        marginTop: '10px',
        marginRight: '10px',
        marginBottom: '10px',
        marginLeft: '10px',
        paddingTop: '10px',
        paddingRight: '10px',
        paddingBottom: '10px',
        paddingLeft: '10px',
        background: '#42b983',
        color: '#ffffff',
        width: '300px',
        borderRadius: '10px 10px 10px 10px',
        fontSize: '18px',
        textAlign: 'center',
        lineHeight: 1.6,
        letterSpacing: '0.02em',
        // boxShadow: '0px'
      },
      baloonPositon : 'bottom',
      baloonPositonSub : 'center',
      subStyle: {
        top: 'inherit',
        left: '50%',
        bottom: '-20px',
        right: 'inherit',
        borderTop: '10px solid #42b983',
        borderRight: '10px solid transparent',
        borderBottom: '10px solid transparent',
        borderLeft: '10px solid transparent',
        margin: '0 0 0 -10px',
        // boxShadow: '0px'
      },
      cs: {
        wrapElement: 'p',
        btElement: 'after',
        exportType: 'scss',
      }
    }
  },
  updated () {
    // main
    this.style.fontSize = this.input.fs + 'px';
    this.style.lineHeight = this.input.lh;
    this.style.letterSpacing = this.input.ls + 'em';
    this.style.color = this.input.color;
    this.style.bgcolor = this.input.bgcolor;

    this.style.marginTop = this.input.mt + 'px';
    this.style.marginRight = this.input.mr + 'px';
    this.style.marginBottom = this.input.mb + 'px';
    this.style.marginLeft = this.input.ml + 'px';

    this.style.paddingTop = this.input.pt + 'px';
    this.style.paddingRight = this.input.pr + 'px';
    this.style.paddingBottom = this.input.pb + 'px';
    this.style.paddingLeft = this.input.pl + 'px';

    this.style.borderRadius = this.input.bt + 'px ' + this.input.br + 'px ' + this.input.bb + 'px ' + this.input.bl + 'px';

    // sub
    if(this.baloonPositon === 'top') {
      this.subStyle.borderTop = this.input.sub.bt + 'px solid transparent';
      this.subStyle.borderRight = this.input.sub.br + 'px solid transparent';
      this.subStyle.borderBottom = this.input.sub.bb + 'px solid ' + this.input.bgcolor;
      this.subStyle.borderLeft = this.input.sub.bl + 'px solid transparent';

      if(this.baloonPositonSub === 'left') {
        this.subStyle.left = this.input.sub.ppl + 'px';
        this.subStyle.margin = '0px';
      } else if(this.baloonPositonSub === 'center') {
        this.subStyle.margin = '0 0 0 -' + (Number(this.input.sub.bl) + Number(this.input.sub.br)) /2 + 'px';
      } else if(this.baloonPositonSub === 'right') {
        this.subStyle.right = this.input.sub.ppr + 'px';
        this.subStyle.margin = '0px';
      }
      this.subStyle.top = '-' + (Number(this.input.sub.bt) + Number(this.input.sub.bb)) + 'px';
    }
    else if(this.baloonPositon === 'right') {
      this.subStyle.borderTop = this.input.sub.bt + 'px solid transparent';
      this.subStyle.borderRight = this.input.sub.br + 'px solid transparent';
      this.subStyle.borderBottom = this.input.sub.bb + 'px solid transparent';
      this.subStyle.borderLeft = this.input.sub.bl + 'px solid ' + this.input.bgcolor;

      if(this.baloonPositonSub === 'top') {
        this.subStyle.top = this.input.sub.ppt + 'px';
        this.subStyle.margin = '0px';
      } else if(this.baloonPositonSub === 'center') {
        this.subStyle.margin = '-' + (Number(this.input.sub.bt) + Number(this.input.sub.bb)) /2 + 'px 0 0 0';
      } else if(this.baloonPositonSub === 'bottom') {
        this.subStyle.bottom = this.input.sub.ppb + 'px';
        this.subStyle.margin = '0px';
      }
      this.subStyle.right = '-' + (Number(this.input.sub.bl) + Number(this.input.sub.br)) + 'px';
    }
    else if(this.baloonPositon === 'bottom') {
      this.subStyle.borderTop = this.input.sub.bt + 'px solid ' + this.input.bgcolor;
      this.subStyle.borderRight = this.input.sub.br + 'px solid transparent';
      this.subStyle.borderBottom = this.input.sub.bb + 'px solid transparent';
      this.subStyle.borderLeft = this.input.sub.bl + 'px solid transparent';

      if(this.baloonPositonSub === 'left') {
        this.subStyle.left = this.input.sub.ppl + 'px';
        this.subStyle.margin = '0px';
      } else if(this.baloonPositonSub === 'center') {
        this.subStyle.margin = '0 0 0 -' + (Number(this.input.sub.bl) + Number(this.input.sub.br)) /2 + 'px';
      } else if(this.baloonPositonSub === 'right') {
        this.subStyle.right = this.input.sub.ppr + 'px';
        this.subStyle.margin = '0px';
      }
      this.subStyle.bottom = '-' + (Number(this.input.sub.bt) + Number(this.input.sub.bb)) + 'px';
    }
    else if(this.baloonPositon === 'left') {
      this.subStyle.borderTop = this.input.sub.bt + 'px solid transparent';
      this.subStyle.borderRight = this.input.sub.br + 'px solid ' + this.input.bgcolor;
      this.subStyle.borderBottom = this.input.sub.bb + 'px solid transparent';
      this.subStyle.borderLeft = this.input.sub.bl + 'px solid transparent';

      if(this.baloonPositonSub === 'top') {
        this.subStyle.top = this.input.sub.ppt + 'px';
        this.subStyle.margin = '0px';
      } else if(this.baloonPositonSub === 'center') {
        this.subStyle.margin = '-' + (Number(this.input.sub.bt) + Number(this.input.sub.bb)) /2 + 'px 0 0 0';
      } else if(this.baloonPositonSub === 'bottom') {
        this.subStyle.bottom = this.input.sub.ppb + 'px';
        this.subStyle.margin = '0px';
      }
      this.subStyle.left = '-' + (Number(this.input.sub.bl) + Number(this.input.sub.br)) + 'px';
    }

    // // shadow
    // this.style.boxShadow = this.input.bsr + 'px ' + this.input.bsb + 'px ' + this.input.bsbl + 'px ' + this.input.bsbl + 'px rgba(' + this.hex2rgb(this.input.bsc) + ', ' + this.input.bso + ')';
    // this.subStyle.boxShadow = this.input.bsr + 'px ' + this.input.bsb + 'px ' + this.input.bsbl + 'px ' + this.input.bsbl + 'px rgba(' + this.hex2rgb(this.input.bsc) + ', ' + this.input.bso + ')';
  },
  methods: {
    toggleClass: function () {
      this.isBgdark = !this.isBgdark;
      this.isBglight = !this.isBglight;
    },
    set: function (type) {
      if(type === 'w' && this.input.w !== 'auto') { this.style.width = this.input.w + 'px'; }
    },
    auto: function (type) {
      if(type === 'w') {
        this.style.width = 'auto';
        this.input.w = 'auto';
      }
    },
    pos: function () {
      if(this.baloonPositon === 'top') {
        this.input.sub.ppb = 'inherit';
        this.input.sub.bt = '0';
        this.subStyle.bottom = this.input.sub.ppb;
      }
      else if(this.baloonPositon === 'right') {
        this.input.sub.ppl = 'inherit';
        this.input.sub.br = '0';
        this.subStyle.left = this.input.sub.ppl;
      }
      else if(this.baloonPositon === 'bottom') {
        this.input.sub.ppt = 'inherit';
        this.input.sub.bb = '0';
        this.subStyle.top = this.input.sub.ppt;
      }
      else if(this.baloonPositon === 'left') {
        this.input.sub.ppr = 'inherit';
        this.input.sub.bl = '0';
        this.subStyle.right = this.input.sub.ppr;
      }
      this.ppos();
    },
    ppos: function () {
      if(this.baloonPositon === 'top' || this.baloonPositon === 'bottom') {
        if(this.baloonPositonSub === 'left') {
          this.input.sub.ppl = '0';
          this.input.sub.ppr = 'inherit';
          this.subStyle.right = this.input.sub.ppr;
        } else if(this.baloonPositonSub === 'center') {
          this.input.sub.ppl = '50%';
          this.input.sub.ppr = 'inherit';
          this.subStyle.left = this.input.sub.ppl;
          this.subStyle.right = this.input.sub.ppr;
        } else if(this.baloonPositonSub === 'right') {
          this.input.sub.ppl = 'inherit';
          this.input.sub.ppr = '0';
          this.subStyle.left = this.input.sub.ppl;
        }
      }
      else if(this.baloonPositon === 'right' || this.baloonPositon === 'left') {
        if(this.baloonPositonSub === 'top') {
          this.input.sub.ppt = '0';
          this.input.sub.ppb = 'inherit';
          this.subStyle.bottom = this.input.sub.ppb;
        } else if(this.baloonPositonSub === 'center') {
          this.input.sub.ppt = '50%';
          this.input.sub.ppb = 'inherit';
          this.subStyle.top = this.input.sub.ppt;
          this.subStyle.bottom = this.input.sub.ppb;
        } else if(this.baloonPositonSub === 'bottom') {
          this.input.sub.ppt = 'inherit';
          this.input.sub.ppb = '0';
          this.subStyle.top = this.input.sub.ppt;
        }
      }
    },
    generate: function () {
      let tag = '';
      let s = '';
      let t = '  ';
      let n = '\n';

      // html tag
      if(this.cs.wrapElement === 'div') {
        tag += '<div class="balloon">' + n
        tag += t + this.sampleText.replace('\n', '<br>') + n
        if(this.cs.btElement === 'span') {
          tag += t + '<span class="tail"></span>' + n
        } else if(this.cs.btElement === 'div') {
          tag += t + '<div class="tail"></div>' + n
        }
        tag += '</div>'
      } else if(this.cs.wrapElement === 'p') {
        tag += '<p class="balloon">' + n
        tag += t + this.sampleText.replace('\n', '<br>') + n
        if(this.cs.btElement === 'span') {
          tag += t + '<span class="tail"></span>' + n
        } else if(this.cs.btElement === 'div') {
          tag += t + '<div class="tail"></div>' + n
        }
        tag += '</p>'
      } else {
        alert('ERROR');
        return;
      }

      // scss and css
      if(this.cs.exportType === 'scss') {
        s += '.balloon {' + n
        s += t + 'position: relative;' + n
        s += t + 'display: inline-block;' + n
        s += t + 'margin: ' + this.style.marginTop + ' ' + this.style.marginRight + ' ' + this.style.marginBottom + ' ' + this.style.marginLeft + '; ' + n
        s += t + 'padding: ' + this.style.paddingTop + ' ' + this.style.paddingRight + ' ' + this.style.paddingBottom + ' ' + this.style.paddingLeft + '; ' + n
        s += t + 'background: ' + this.style.background + '; ' + n
        s += t + 'color: ' + this.style.color + '; ' + n
        if(this.style.width !== 'auto') {
          s += t + 'width: ' + this.style.width + '; ' + n
        }
        s += t + 'border-radius: ' + this.style.borderRadius + '; ' + n
        s += t + 'font-size: ' + this.style.fontSize + '; ' + n
        s += t + 'text-align: ' + this.style.textAlign + '; ' + n
        s += t + 'line-height: ' + this.style.lineHeight + '; ' + n
        s += t + 'letter-spacing: ' + this.style.letterSpacing + ';' + n
        s += t + 'word-break: break-all;' + n

        if(this.cs.btElement === 'div' || this.cs.btElement === 'span') {
          s += t + '.tail {' + n
        } else if(this.cs.btElement === 'before') {
          s += t + '&::before {' + n
          s += t + t + "content: '';" + n
        } else if(this.cs.btElement === 'after') {
          s += t + '&::after {' + n
          s += t + t + "content: '';" + n
        }
        s += t + t + 'display: block;' + n
        s += t + t + 'width: 0px;' + n
        s += t + t + 'height: 0px;' + n
        s += t + t + 'position: absolute;' + n
        if(this.subStyle.top !== 'inherit') {
          s += t + t + 'top: ' + this.subStyle.top + ';' + n
        }
        if(this.subStyle.bottom !== 'inherit') {
          s += t + t + 'bottom: ' + this.subStyle.bottom + ';' + n
        }
        if(this.subStyle.left !== 'inherit') {
          s += t + t + 'left: ' + this.subStyle.left + ';' + n
        }
        if(this.subStyle.right !== 'inherit') {
          s += t + t + 'right: ' + this.subStyle.right + ';' + n
        }
        if(this.subStyle.margin !== '0px') {
          s += t + t + 'margin: ' + this.subStyle.margin + ';' + n
        }
        s += t + t + 'border-top: ' + this.subStyle.borderTop + ';' + n
        s += t + t + 'border-right: ' + this.subStyle.borderRight + ';' + n
        s += t + t + 'border-bottom: ' + this.subStyle.borderBottom + ';' + n
        s += t + t + 'border-left: ' + this.subStyle.borderLeft + ';' + n
        s += t + '}' + n
        s += '}'
      } else if(this.cs.exportType === 'css') {
        s += '.balloon {' + n
        s += t + 'position: relative;' + n
        s += t + 'display: inline-block;' + n
        s += t + 'margin: ' + this.style.marginTop + ' ' + this.style.marginRight + ' ' + this.style.marginBottom + ' ' + this.style.marginLeft + '; ' + n
        s += t + 'padding: ' + this.style.paddingTop + ' ' + this.style.paddingRight + ' ' + this.style.paddingBottom + ' ' + this.style.paddingLeft + '; ' + n
        s += t + 'background: ' + this.style.background + '; ' + n
        s += t + 'color: ' + this.style.color + '; ' + n
        if(this.style.width !== 'auto') {
          s += t + 'width: ' + this.style.width + '; ' + n
        }
        s += t + 'border-radius: ' + this.style.borderRadius + '; ' + n
        s += t + 'font-size: ' + this.style.fontSize + '; ' + n
        s += t + 'text-align: ' + this.style.textAlign + '; ' + n
        s += t + 'line-height: ' + this.style.lineHeight + '; ' + n
        s += t + 'letter-spacing: ' + this.style.letterSpacing + ';' + n
        s += t + 'word-break: break-all;' + n

        s += '}' + n
        if(this.cs.btElement === 'div' || this.cs.btElement === 'span') {
          s += '.balloon .tail {' + n
        } else if(this.cs.btElement === 'before') {
          s += '.balloon::before {' + n
          s += t + "content: '';" + n
        } else if(this.cs.btElement === 'after') {
          s += '.balloon::after {' + n
          s += t + "content: '';" + n
        }
        s += t + 'display: block;' + n
        s += t + 'width: 0px;' + n
        s += t + 'height: 0px;' + n
        s += t + 'position: absolute;' + n
        if(this.subStyle.top !== 'inherit') {
          s += t + 'top: ' + this.subStyle.top + ';' + n
        }
        if(this.subStyle.bottom !== 'inherit') {
          s += t + 'bottom: ' + this.subStyle.bottom + ';' + n
        }
        if(this.subStyle.left !== 'inherit') {
          s += t + 'left: ' + this.subStyle.left + ';' + n
        }
        if(this.subStyle.right !== 'inherit') {
          s += t + 'right: ' + this.subStyle.right + ';' + n
        }
        if(this.subStyle.margin !== '0px') {
          s += t + 'margin: ' + this.subStyle.margin + ';' + n
        }
        s += t + 'border-top: ' + this.subStyle.borderTop + ';' + n
        s += t + 'border-right: ' + this.subStyle.borderRight + ';' + n
        s += t + 'border-bottom: ' + this.subStyle.borderBottom + ';' + n
        s += t + 'border-left: ' + this.subStyle.borderLeft + ';' + n
        s += '}' + n
      } else {
        alert('ERROR');
        return;
      }
      this.isComplete = true;
      $('#html').val(tag);
      $('#source').val(s);
    }
    // hex2rgb: function ( hex ) {
    //   if ( hex.slice(0, 1) == "#" ) hex = hex.slice(1) ;
    //   if ( hex.length == 3 ) hex = hex.slice(0,1) + hex.slice(0,1) + hex.slice(1,2) + hex.slice(1,2) + hex.slice(2,3) + hex.slice(2,3) ;

    //   return [ hex.slice( 0, 2 ), hex.slice( 2, 4 ), hex.slice( 4, 6 ) ].map( function ( str ) {
    //     return parseInt( str, 16 ) ;
    //   } ) ;
    // }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
$green: #20c4cb;
$gray: #eeeeee;

$tw: 80px;
$op: 60px;

.box {
  display: flex;
  .unit {
    width: 50%;
    max-width: 50%;
    max-height: calc(100vh - 160px - 114px);
    overflow: auto;
  }
  .canvas {
    position: relative;
    text-align: center;
    padding: 40px;
    &.bgdark {
      background-color: #666666;
      background-size: 40px 40px;
      background-position: 0 0, 20px 20px;
      background-image: linear-gradient(45deg,  #333333 25%, #333333 25%, transparent 25%, transparent 75%, #333333 75%, #333333 75%),
                        linear-gradient(-135deg, #333333 25%, #333333 25%, transparent 25%, transparent 75%, #333333 75%, #333333 75%);
    }
    &.bglight {
      background-color: #eeeeee;
      background-size: 40px 40px;
      background-position: 0 0, 20px 20px;
      background-image: linear-gradient(45deg,  #dcdcdc 25%, #dcdcdc 25%, transparent 25%, transparent 75%, #dcdcdc 75%, #dcdcdc 75%),
                        linear-gradient(-135deg, #dcdcdc 25%, #dcdcdc 25%, transparent 25%, transparent 75%, #dcdcdc 75%, #dcdcdc 75%);
    }
    .wrap {
      display: inline-block;
      border: 1px solid #fff;
    }
    .bottons {
      position: absolute;
      left: 4px;
      bottom: 4px;
      button {
        padding: 6px;
        background: #fff;
        font-size: 1rem;
      }
    }
  }
  .controller {
    padding: 0 40px;
    text-align: left;

    input,
    textarea {
      padding: 14px;
      border-radius: 6px;
      background: $gray;
      font-size: 1.6rem;
    }
    textarea {
      width: 100%;
      height: 44px;
      resize: none;
    }
    input[type=range] {
      appearance: none;
      margin: 10px 0;
      padding: 0;
      width: calc(100% - #{$tw} - #{$op});
      height: 4px;
      background-color: $gray;
      &::-webkit-slider-thumb {
        appearance: none;
        cursor: pointer;
        position: relative;
        border: none;
        width: 6px;
        height: 20px;
        display: block;
        background-color: $green;
        border-radius: 6px;
      }
    }
    output {
      display: inline-block;
      width: $op;
      text-align: center;
    }
    button {
      display: block;
      padding: 10px 14px;
      border-radius: 6px;
      background: $green;
      color: #fff;
    }
    select {
      padding: 14px;
      background: $gray;
      border-radius: 6px;
      width: 100%;
    }
    label {
      display: inline-block;
      margin-bottom: 30px;
      cursor: pointer;
      + label {
        margin-left: 20px;
      }
      input {
        display: inline-block;
      }
    }
    p {
      + p {
        margin-top: 10px;
      }
      + h2 {
        margin-top: 40px;
      }
      span {
        display: inline-block;
        width: $tw;
      }
    }
    div + h2 {
      margin-top: 40px;
    }
  }
  .arrow {
    position: relative;
    &::before {
      content: '';
      width: 6px;
      height: 6px;
      border: 3px solid $green;
      border-top: 0px;
      border-left: 0px;
      position: absolute;
      right: 14px;
      top: 50%;
      margin-top: -5px;
      transform: rotate(45deg);
    }
  }
  .samplebox {
    margin-left: 10px;
    width: 100px !important;
    height: 40px;
    border: 1px solid $gray;
    vertical-align: middle;
    line-height: 40px;
    text-align: center;
  }
  .sub {
    margin-top: 20px;
    margin-left: 20px;
    padding: 20px;
    background: $gray;
    border-radius: 6px;
    input[type=range] {
      background-color: #fff;
    }
    select {
      padding: 10px;
      background: #fff;
      border-radius: 6px;
    }
    p {
      + h3 {
        margin-top: 30px;
      }
    }
    div + h3 { margin-top: 30px; }
  }
  .inbox {
    margin: 20px 0 0;
  }
}
.balloon {
  position: relative;
  word-break: break-all;
  // white-space: pre-line;
  .tail {
    display: block;
    position: absolute;
    width: 0px;
    height: 0px;
  }
}
.box2 {
  margin-top: 40px;
  h3 {
    margin-top: 20px;
  }
  dl {
    display: flex;
    width: 100%;
    + dl {
      margin-top: 10px;
    }
    dt {
      width: 200px;
      font-size: 1.8rem;
      font-weight: bold;
    }
  }
  label {
    cursor: pointer;
    + label {
      margin-left: 20px;
    }
  }
  button {
    display: block;
    margin: 20px 0 0 auto;
    padding: 14px 100px;
    border-radius: 10px;
    background: $green;
    color: #fff;
  }
  .unit {
    width: 50%;
    margin: 0 0 0 auto;
  }
  textarea {
    padding: 14px;
    border-radius: 6px;
    background: $gray;
    font-size: 1.6rem;
    width: 100%;
    height: 500px;
    &.mini {
      height: 100px;
    }
  }
}
#complete {
  display: none;
  &.block {
    display: block;
  }
}
</style>


